'use client';

import { useEffect, useState } from 'react';
import { AuthService, TokenManager, User } from '@/lib/auth';
import { useRouter } from 'next/navigation';

export default function Dashboard() {
  const router = useRouter();
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadUser = async () => {
      try {
        if (!TokenManager.isAuthenticated()) {
          router.push('/signup');
          return;
        }

        const response = await AuthService.getCurrentUser();
        if (response.data) {
          setUser(response.data);
        }
      } catch (error) {
        console.error('Failed to load user:', error);
        TokenManager.removeToken();
        router.push('/signup');
      } finally {
        setLoading(false);
      }
    };

    loadUser();
  }, [router]);

  const handleLogout = async () => {
    try {
      await AuthService.logout();
      router.push('/');
    } catch (error) {
      console.error('Logout error:', error);
      // Still redirect even if logout API fails
      TokenManager.removeToken();
      router.push('/');
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-black text-white flex items-center justify-center">
        <div className="text-center">
          <div className="text-6xl mb-4 animate-float">üöó</div>
          <p className="text-gray-400">Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black text-white">
      {/* Background elements */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-20 left-10 w-72 h-72 bg-[#2ec8c6] rounded-full opacity-10 blur-3xl animate-float"></div>
        <div className="absolute bottom-20 right-10 w-96 h-96 bg-[#2ec8c6] rounded-full opacity-10 blur-3xl animate-float" style={{ animationDelay: '2s' }}></div>
      </div>

      {/* Navigation */}
      <nav className="relative z-50 glass-strong">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div className="flex items-center space-x-2">
            <div className="w-10 h-10 bg-gradient-to-br from-[#2ec8c6] to-[#1a9a99] rounded-lg flex items-center justify-center text-2xl">
              üöó
            </div>
            <span className="text-2xl font-bold gradient-text">MotorTrust</span>
          </div>
          <button
            onClick={handleLogout}
            className="glass hover:glass-strong text-white font-semibold px-6 py-2 rounded-full transition-all transform hover:scale-105"
          >
            Logout
          </button>
        </div>
      </nav>

      <div className="relative z-10 max-w-7xl mx-auto px-6 py-12">
        {/* Welcome Section */}
        <div className="glass-strong rounded-3xl p-8 md:p-12 mb-8">
          <h1 className="text-5xl font-bold gradient-text mb-4">
            Welcome back, {user?.name}!
          </h1>
          <p className="text-gray-400 text-xl">
            You&apos;re logged in as a <span className="text-[#2ec8c6] font-semibold capitalize">{user?.role.replace('_', ' ')}</span>
          </p>
        </div>

        {/* User Info Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          <div className="glass rounded-2xl p-6">
            <div className="text-4xl mb-4">üìß</div>
            <div className="text-gray-400 text-sm mb-1">Email</div>
            <div className="font-semibold">{user?.email}</div>
          </div>

          {user?.phone_number && (
            <div className="glass rounded-2xl p-6">
              <div className="text-4xl mb-4">üì±</div>
              <div className="text-gray-400 text-sm mb-1">Phone</div>
              <div className="font-semibold">{user.phone_number}</div>
            </div>
          )}

          {user?.city && user?.state && (
            <div className="glass rounded-2xl p-6">
              <div className="text-4xl mb-4">üìç</div>
              <div className="text-gray-400 text-sm mb-1">Location</div>
              <div className="font-semibold">{user.city}, {user.state}</div>
            </div>
          )}
        </div>

        {/* Quick Actions */}
        <div className="glass-strong rounded-3xl p-8">
          <h2 className="text-3xl font-bold mb-6">Quick Actions</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {[
              { icon: 'üöó', title: 'Add Vehicle', desc: 'Register a new vehicle' },
              { icon: 'üîç', title: 'AI Diagnosis', desc: 'Get instant diagnosis' },
              { icon: 'üîß', title: 'Find Repair Shop', desc: 'Connect with shops' },
              { icon: 'üìä', title: 'View History', desc: 'Check service records' }
            ].map((action, idx) => (
              <button
                key={idx}
                className="glass hover:glass-strong rounded-xl p-6 text-left transition-all transform hover:scale-105 hover:glow"
              >
                <div className="text-4xl mb-3">{action.icon}</div>
                <div className="font-bold text-lg mb-1">{action.title}</div>
                <div className="text-gray-400 text-sm">{action.desc}</div>
              </button>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
